:host {
  display: inline-flex;
}

:host .star-rating {
  display: inline-flex;
  align-items: center;
  flex-direction: row;
  gap: var(--mdx-comp-star-rating-container-spacing-gap);

  &__sizing-small {
    height: var(--mdx-comp-star-rating-icon-sizing-small);
  }

  &__sizing-large {
    height: var(--mdx-comp-star-rating-icon-sizing-large);
  }

  &__multiple-stars {
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    gap: var(--mdx-comp-star-rating-stars-spacing-gap-star-row);
  }

  &__star-colored {
    color: var(--mdx-comp-star-rating-stars-selected-color-default);
  }

  &__star-uncolored {
    color: var(--mdx-comp-star-rating-stars-unselected-color-default);
  }

  &__trailing {
    display: inline-flex;
    align-items: center;
    vertical-align: middle;
    gap: var(--mdx-comp-star-rating-trailing-spacing-gap-numbers);

    &__counter-text {
      color: var(--mdx-comp-star-rating-trailing-counter-text-color-default);
      font: var(--mdx-comp-star-rating-trailing-counter-text-font-default);
      font-family: var(--mdx-comp-star-rating-trailing-counter-text-font-default-font-family);
      line-height: var(--mdx-comp-star-rating-trailing-counter-text-font-default-line-height);
      font-size: var(--mdx-comp-star-rating-trailing-counter-text-font-default-font-size);
      letter-spacing: var(--mdx-comp-star-rating-trailing-counter-text-font-default-letter-spacing);
      text-transform: var(--mdx-comp-star-rating-trailing-counter-text-font-default-text-case);
      text-decoration: var(--mdx-comp-star-rating-trailing-counter-text-font-default-text-decoration);
      font-weight: var(--mdx-comp-star-rating-trailing-counter-text-font-weight-default);
      margin: 0;
    }
    &__counter-text:hover {
      color: var(--mdx-comp-star-rating-trailing-counter-text-color-hover);
    }

    &__counter-link {
      color: var(--mdx-comp-star-rating-trailing-counter-link-color-default);
      font: var(--mdx-comp-star-rating-trailing-counter-link-font-default);
      font-family: var(--mdx-comp-star-rating-trailing-counter-link-font-default-font-family);
      line-height: var(--mdx-comp-star-rating-trailing-counter-link-font-default-line-height);
      font-size: var(--mdx-comp-star-rating-trailing-counter-link-font-default-font-size);
      letter-spacing: var(--mdx-comp-star-rating-trailing-counter-link-font-default-letter-spacing);
      text-transform: var(--mdx-comp-star-rating-trailing-counter-link-font-default-text-case);
      font-weight: var(--mdx-comp-star-rating-trailing-counter-link-font-weight-default);
      text-decoration: var(--mdx-comp-star-rating-trailing-counter-link-text-decoration-default);
      margin: 0;
    }
    &__counter-link:hover {
      color: var(--mdx-comp-star-rating-trailing-counter-link-color-hover);
    }

    &__average-rating {
      color: var(--mdx-comp-star-rating-trailing-average-rating-color-default);
      font: var(--mdx-comp-star-rating-trailing-average-rating-font-default);
      font-family: var(--mdx-comp-star-rating-trailing-average-rating-font-default-font-family);
      line-height: var(--mdx-comp-star-rating-trailing-average-rating-font-default-line-height);
      font-size: var(--mdx-comp-star-rating-trailing-average-rating-font-default-font-size);
      letter-spacing: var(--mdx-comp-star-rating-trailing-average-rating-font-default-letter-spacing);
      text-transform: var(--mdx-comp-star-rating-trailing-average-rating-font-default-text-case);
      font-weight: var(--mdx-comp-star-rating-trailing-average-rating-font-weight-default);
      text-decoration: var(--mdx-comp-star-rating-trailing-average-rating-text-decoration-default);
      margin: 0;
    }
    &__average-rating:hover {
      color: var(--mdx-comp-star-rating-trailing-average-rating-color-hover);
    }

    &__counter-bracket {
      color: var(--mdx-comp-star-rating-trailing-counter-bracket-color-default);
      font: var(--mdx-comp-star-rating-trailing-counter-bracket-font-default);
      font-family: var(--mdx-comp-star-rating-trailing-counter-bracket-font-default-font-family);
      font-weight: var(--mdx-comp-star-rating-trailing-counter-bracket-font-default-font-weight);
      line-height: var(--mdx-comp-star-rating-trailing-counter-bracket-font-default-line-height);
      font-size: var(--mdx-comp-star-rating-trailing-counter-bracket-font-default-font-size);
      letter-spacing: var(--mdx-comp-star-rating-trailing-counter-bracket-font-default-letter-spacing);
      text-transform: var(--mdx-comp-star-rating-trailing-counter-bracket-font-default-text-case);
      text-decoration: var(--mdx-comp-star-rating-trailing-counter-bracket-font-default-text-decoration);
      margin: 0;
    }
    &__counter-bracket:hover {
      color: var(--mdx-comp-star-rating-trailing-counter-bracket-color-hover);
    }
  }
}

