import { Component, Fragment, h, Prop } from '@stencil/core';
import { ICON_SIZES } from '../icons/exports';
import { STAR_RATING_SIZES, STAR_RATING_VARIANTS } from './export';

@Component({
  tag: 'mdx-star-rating',
  styleUrl: 'star-rating.scss',
  shadow: true,
})
export class StarRating {
  @Prop()
  variant: (typeof STAR_RATING_VARIANTS)[keyof typeof STAR_RATING_VARIANTS] = STAR_RATING_VARIANTS.COUNTER;

  @Prop()
  size: (typeof STAR_RATING_SIZES)[keyof typeof STAR_RATING_SIZES] = STAR_RATING_SIZES.SM;

  /**
   * Number from 0 to 5
   */
  @Prop()
  value = 0;

  @Prop()
  ratingCount = 0;

  @Prop()
  // eslint-disable-next-line @stencil-community/reserved-member-names
  ariaDescription?: string;

  @Prop()
  ratingLink?: string;

  @Prop()
  ratingLinkTitle?: string = 'go to rating detail';

  @Prop()
  ratingLinkTarget: '_blank' | '_self' | '_parent' | '_top' = '_self';

  private generateStars(starCount: number, value: number) {
    return (
      <div class="star-rating__multiple-stars">
        {[...Array(starCount).keys()].map((index) => {
          return this.getStar(index + 1 - value);
        })}
      </div>
    );
  }

  private getStar(value: number) {
    const starSize: (typeof ICON_SIZES)[keyof typeof ICON_SIZES] =
      this.size === STAR_RATING_SIZES.LG ? ICON_SIZES.SM : ICON_SIZES.XS;
    if (value > 0.75) {
      return <mdx-icon-star class="star-rating__star-uncolored" size={starSize} />;
    } else if (value > 0.25) {
      return <mdx-icon-half-star class="star-rating__star-colored" size={starSize} />;
    }
    return <mdx-icon-filled-star class="star-rating__star-colored" size={starSize} />;
  }

  private getCountFromVariant() {
    switch (this.variant) {
      case STAR_RATING_VARIANTS.COUNTER_LINK:
        return (
          <a title={this.ratingLinkTitle} href={this.ratingLink} target={this.ratingLinkTarget}>
            <p class="star-rating__trailing__counter-link">{this.ratingCount}</p>
          </a>
        );
      case STAR_RATING_VARIANTS.COUNTER:
        return <p class="star-rating__trailing__counter-text">{this.ratingCount}</p>;
      case STAR_RATING_VARIANTS.RATING:
        return <p class="star-rating__trailing__counter-bracket">({this.ratingCount})</p>;
      default:
        return <Fragment />;
    }
  }

  render() {
    return (
      <div
        class={`star-rating ${this.size === STAR_RATING_SIZES.LG ? 'star-rating__sizing-large' : 'star-rating__sizing-small'}`}
        aria-description={this.ariaDescription || `star rating; ${this.value} of 5 stars; ${this.ratingCount} ratings`}
      >
        {this.generateStars(5, this.value)}
        <div class="star-rating__trailing">
          {this.variant === STAR_RATING_VARIANTS.RATING && (
            <a title={this.ratingLinkTitle} href={this.ratingLink} target={this.ratingLinkTarget}>
              <p class="star-rating__trailing__average-rating">{this.value}</p>
            </a>
          )}
          {this.getCountFromVariant()}
        </div>
      </div>
    );
  }
}
